import './App.css';
import Sidebar from './components/Sidebar'
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom'
import Home from './pages/Home';
import GeometrySDMenu from './pages/sd/GeometriMenu'
import KalkulusSMAMenu from './pages/sma/KalkulusMenu';
import Eksponensial from './pages/sma/Eksponensial';
import SimetriGeo from './pages/sd/SimetriGeo';
import Jarak from './pages/sma/Jarak';
import GeometriSMAMenu from './pages/sma/GeometriSMA';
import Pecahan from './pages/smp/Pecahan';
import AljabarMenu from './pages/smp/AljabarMenu';
import Segitiga from './pages/sd/Segitiga';

function App() {
  return (
    <>
    <Router>
      <Sidebar />
      <Switch>

        {/* SD */}        
        <Route path="/" exact component={Home}/>
        <Route path="/sd/geometri"  exact component={GeometrySDMenu}/>
        <Route path="/sd/geometri/segitiga" exact  component={Segitiga}/>
        <Route path="/sd/geometri/simetri" exact  component={SimetriGeo}/>

        {/* SMP */}
        <Route path="/smp/aljabar"  exact component={AljabarMenu}/>
        <Route path="/smp/aljabar/pecahan"  exact component={Pecahan}/>


        {/* SMA */}        
        <Route path="/sma/kalkulus" exact  component={KalkulusSMAMenu}/>
        <Route path="/sma/geometri" exact  component={GeometriSMAMenu}/>
        <Route path="/sma/kalkulus/eksponensial" exact  component={Eksponensial}/>
        <Route path="/sma/geometri/jarak" exact  component={Jarak}/>
      </Switch>
    </Router>
    </>
  );
}

export default App;
