import React from 'react';
import './Iframe.css'

const Iframe = ({ source }) => {

    if (!source) {
        return <div>Loading...</div>;
    }

    const src = source;     
    return (
        // basic bootstrap classes. you can change with yours.
            <div className="geogebra-iframe">
                <iframe src={src}></iframe>
            </div>
    );
};

export default Iframe;